<template>
  <div class="findPassword" v-if="forgotPassword === true">
    <div class="card_wrapper">
      <div class="cancel" @click="onClickCloseBtn">X</div>
      <div class="content_wrapper">
        <p>  ?</p>
            <!--   email -->
        <input placeholder=" " type="text" v-model="email_validate" />
        <button @click="onClickSendValidate">  </button>
        <!--    -->
      </div>
    </div>
  </div>
</template>

<script>
import '@/assets/style/account_password.scss'
import { ref } from 'vue'
import { useStore } from 'vuex'


export default {
  name: "AccountPassword",
  props: {
    forgotPassword : {
      type: Boolean,
      required: true,
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    const email_validate = ref('')
    
    const onClickCloseBtn = function() {
      // email_validate = ref('')
      emit('closeDialog')
    }

    const onClickSendValidate = function() {
      store.dispatch('sendValidateEmail', {email: email_validate.value})
      .then(function(){
        console.log('')
        onClickCloseBtn()
      })
      .catch(function (){
        console.log('  ')
      })
    }


    return {
      email_validate,
      onClickSendValidate,
      onClickCloseBtn
    }
  }

}
</script>

